import './StatTable.scss';

import MobileSearch from '../Search/MobileSearch';
import Search from '../Search/Search';
import StatRow from '../Stat/StatRow';

const StatTable = ({stat,filter,filterSet,selectedItemSet}) => (
  
  <>
    <Search  filter={filter} filterSet={filterSet}/>
    <section className="wrapper">
      <MobileSearch filter={filter} filterSet={filterSet}/>
      <main className="row title">
        <ul>
          <li>Country</li>
          <li>Infected</li>
          <li>Tested</li>
          <li>Recovered</li>
          <li>Deceased</li>
        </ul>
      </main>
      {/* {console.log(filter)} */}
      {stat
      .filter((stat) => stat.country.includes(filter))
      .slice(0,10)
      .map((stat) => (
        <StatRow stat={stat} key={stat.country}
          onSelect={(stat) => selectedItemSet(stat)}
        />
      ))}
      
    </section>
  </>

);

export default StatTable;