import './News.scss';

const News = () => (
    
    <div className="profileCard">
        <div className="profileCard__imageWrap" style={{backgroundImage: 'url(https://farm9.staticflickr.com/8682/15309989834_7ef83fcfca_z.jpg)'}}>
            <div className="profileCard__location"><i className="fa fa-map-pin"></i></div>
            <div className="profileCard__thumbWrap"><img className="profileCard__thumb" src="https://farm4.staticflickr.com/3022/3009079316_3caf8c7655_q.jpg"/></div>
            <header className="profileCard__header">
            <h1 className="profileCard__title">Carmelita Franco Anticio</h1>
            <div className="profileCard__subTitle">Fashion photographer</div>
            </header>
        </div>
        <div className="profileCard__contentWrap">
            <div className="profileCard__content"> 
            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pellentesque sit amet euismod justo. Cras malesuada vel mi vitae varius. Cras porttitor nisi in tempus congue. Suspendisse feugiat nisi at est volutpat, et consequat nulla iaculis. Donec cursus ligula massa, at ullamcorper orci mollis vitae.</p>
            </div>
        </div>
        <ul className="profileCard__social">
            <li className="profileCard__socialItem"><a className="profileCard__socialLink" href="#"><i className="fa fa-facebook"></i></a></li>
            <li className="profileCard__socialItem"><a className="profileCard__socialLink" href="#"><i className="fa fa-twitter"></i></a></li>
            <li className="profileCard__socialItem"><a className="profileCard__socialLink" href="#"><i className="fa fa-instagram"></i></a></li>
            <li className="profileCard__socialItem"><a className="profileCard__socialLink" href="#"><i className="fa fa-link"></i></a></li>
            <li className="profileCard__socialItem"><a className="profileCard__socialLink" href="#"><i className="fa fa-envelope"></i></a></li>
        </ul>
    </div>
);

export default News;