import React from 'react';

import StatCard from '../components/Stat/Card/StatCard';
import StatTable from '../components/Stat/StatTable';
import stat from '../stat.json';

const Cases = () => {
    
    const [filter,filterSet] = React.useState("");
    const [selectedItem,selectedItemSet] = React.useState("");
    return (
        <>
            <div style={{
                display: "grid",
                gridTemplateColumns: "60% 40%",
                gridColumnGap: "2rem",
            }} >
                <div>
                    <StatTable stat={stat} filter={filter} filterSet={filterSet} selectedItemSet={selectedItemSet}/>
                </div>
                <div>
                    <StatCard selectedItem={selectedItem} selectedItemSet={selectedItemSet}/>
                </div>
            </div>
        </>
    )
}

export default Cases;