import './Slider.scss';

const Slider = ({selectedItem}) => (
    <>
        <div id="slider">
            <ul>
                <li>
                <div class="slider-container">
                    <p>Infected: {selectedItem.infected}</p>
                    <p>Tested: {selectedItem.tested}</p>
                    <p>Recovered: {selectedItem.recovered}</p>
                </div>
                </li>
            </ul>
        </div>
    </>
)


export default Slider;