import './search.scss';

import React from 'react';

import $ from 'jquery';

const Search = ({filter,filterSet}) => (
    <>
        <link rel="stylesheet" href="//maxcdn.bootstrapcdn.com/font-awesome/4.5.0/css/font-awesome.min.css"/>
        <div className='mobileSearch'>
            
            <div className="searchBox-fakeInput">
                <div className="searchBox-inputWrapper">
                <input value={filter} onChange={(evt) => filterSet(evt.target.value)} type="text" className="form-control searchBox-input js-searchBox-input" placeholder="Search"/>
                </div>
                <div className="searchBox-clearWrapper">
                <span className="searchBox-clear js-clearSearchBox"><i className="fa fa-times-circle"></i></span>
                </div>
            </div>
            {/* <input value={filter} onChange={(evt) => filterSet(evt.target.value)}/> */}
        </div>
    </>
);

$('.js-clearSearchBox').css('opacity', '0');

$('.js-searchBox-input').focus(function() {
  $('.searchBox-fakeInput').toggleClassName("is-focussed");
});

$('.js-searchBox-input').keyup(function() {
  if ($(this).val() !=='' ) {
    $('.js-clearSearchBox').css('opacity', '1');
  } else {
    $('.js-clearSearchBox').css('opacity', '0');
  };
  
  $(window).bind('keydown', function(e)  {
    if(e.keyCode === 27) {
      $('.js-searchBox-input').val('');
    };
  });
});
// click the button 
$('.js-clearSearchBox').click(function() {
  $('.js-searchBox-input').val('');
  $('.js-searchBox-input').focus();
  $('.js-clearSearchBox').css('opacity', '0');
});

export default Search;