

const StatRow = ({stat, onSelect}) => (
    
  <section className="row-fadeIn-wrapper">
    <article className="row fadeIn nfl">
      <ul>
        <li><button onClick={() => onSelect(stat)}>{stat.country}</button></li>
        <li>{stat.infected}</li>
        <li>{stat.tested}</li>
        <li>{stat.recovered}</li>
        <li>{stat.deceased}</li>
      </ul>
    </article>
  </section>
);

export default StatRow;