import './search.scss';

import React from 'react';

const Search = ({filter,filterSet}) => (
    <>
        <link rel="stylesheet" href="//maxcdn.bootstrapcdn.com/font-awesome/4.5.0/css/font-awesome.min.css"/>
        <div className='destopSearch'>
            <div className="search-area">
                <input type="checkbox" id="ara-chechk"/>
                <label className="search-icon" htmlFor="ara-chechk"></label>
                <div className="search-input">
                    <div className="search-border">
                            <input type="search" name="Arama" id="ara" value={filter} onChange={(evt) => filterSet(evt.target.value)}/>
                            <span></span>
                    </div>
                </div>
            </div>
        </div>
    </>
);

export default Search;